import { Linking } from 'react-native';

const PINNED_URL = "https://twitter.com/PegsolitireBot/status/1783810041122099451";
const HASHTAGS = "ボードゲーム,ペグソリティア";
const TWITTER_URLS = require("./article-urls.json");

function sharePuzzle(puzzle: string) {
  // 記事のURLを取得
  const url = getArticleUrl(puzzle);

  // 記事が見つからなかったらfalseを返す
  if (!url) return false;

  // ツイート内容をカスタマイズ
  const tweetText = "ここにコメント";

  // URLエンコーディングを施したツイート用URLを生成
  const twitterUrl = `https://twitter.com/intent/tweet?text=${encodeURIComponent(tweetText)}&url=${encodeURIComponent(url)}&hashtags=${encodeURIComponent(HASHTAGS)}`;

  // ブラウザでURLを開く
  Linking.openURL(twitterUrl).catch(err => console.error("An error occurred", err));

  return true;
}

function getArticleUrl(puzzle: string) {
  // const TWITTER_URLS: { [key: string]: string } = {
  //   "pqsty": PINNED_URL,
  //   "xyzFM": PINNED_URL,
  //   "kpqrst": PINNED_URL,
  //   "wxzAFM": PINNED_URL,
  //   "kqrsyF": PINNED_URL,
  //   "rwxzAF": PINNED_URL,
  //   "xyzEGM": PINNED_URL,
  // }
  return TWITTER_URLS[puzzle];
}

export { sharePuzzle };

